/* Home Screen */
.home-container {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-bottom: 50px;
    background-color: #ffffff;
    box-sizing: border-box;
    position: relative;
    overflow: hidden;
}

/* 메인 타이틀 */
.main-title {
    font-size: 48px;
    font-weight: 700;
    color: #333;
    margin: 0 0 40px 0;
    letter-spacing: 4px;
}

/* Snow effect */
.snow-container {
    position: absolute;
    inset: 0;
    pointer-events: none;
}

.snow {
    position: absolute;
    top: -10px;
    width: 6px;
    height: 6px;
    background: #fff;
    border-radius: 50%;
    animation: fall linear infinite;
    box-shadow: 0 0 5px rgba(255, 255, 255, 0.5);
}

@keyframes fall {
    0% {
        transform: translateY(-10px) translateX(0);
    }

    100% {
        transform: translateY(100vh) translateX(20px);
    }
}

/* Bottom section */
.bottom-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 16px;
    z-index: 10;
}

.start-btn {
    padding: 14px 40px;
    font-size: 18px;
    font-weight: 600;
    color: #fff;
    background: #333;
    border: none;
    border-radius: 30px;
    cursor: pointer;
    transition: all 0.2s;
}

.start-btn:hover {
    background: #555;
    transform: scale(1.05);
}

.credits {
    margin: 0;
    font-size: 13px;
    color: #666;
}

.test-btn {
    padding: 10px 24px;
    font-size: 14px;
    font-weight: 500;
    color: #fff;
    background: rgba(255, 255, 255, 0.2);
    border: 1px solid rgba(255, 255, 255, 0.4);
    border-radius: 20px;
    cursor: pointer;
    backdrop-filter: blur(5px);
    transition: all 0.2s;
}

.test-btn:hover {
    background: rgba(255, 255, 255, 0.3);
}