.frame-select-wrapper {
    min-height: 100vh;
    width: 100%;
    padding: 40px 20px 120px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 32px;
    background: #f5f5f7;
}

.slick-slide>div {
    display: flex;
    justify-content: center;
}

.slick-slider {
    width: 100%;
    max-width: 700px;
}

.frame-wrapper {
    padding: 8px;
}

.frame-thumbnail {
    width: 140px;
    height: 140px;
    object-fit: cover;
    border-radius: 16px;
    cursor: pointer;
    transition: transform 0.2s ease, box-shadow 0.2s ease;
    border: 3px solid transparent;
}

.frame-thumbnail:hover {
    transform: scale(1.03);
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.12);
}

.frame-thumbnail.selected {
    border-color: #ff9500;
    box-shadow: 0 4px 16px rgba(255, 149, 0, 0.25);
}

.check-btn {
    position: fixed;
    top: 20px;
    right: 20px;
    width: 44px;
    height: 44px;
    padding: 0;
    color: #c7c7cc;
    background: #fff;
    border: none;
    border-radius: 50%;
    cursor: not-allowed;
    transition: all 0.2s ease;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
}

.check-btn.active {
    background: #ff9500;
    color: #fff;
    cursor: pointer;
    box-shadow: 0 4px 12px rgba(255, 149, 0, 0.3);
}

.check-btn.active:hover {
    transform: scale(1.05);
    box-shadow: 0 6px 16px rgba(255, 149, 0, 0.4);
}

.check-btn:disabled {
    cursor: not-allowed;
}