.preview-frame {
    margin: 0 auto 64px;
    position: relative;
    width: min(90vw, 520px);
    max-width: 520px;
    max-height: 70vh;
    height: auto;
    background-color: #e7e7e7;
    overflow: hidden;
    border-radius: 12px;
}

.preview-frame::before {
    content: "";
    display: block;
    padding-top: 150%;
    /* 3:2 ratio */
}

.frame-slot {
    position: absolute;
    overflow: hidden;
    z-index: 1;
    border-radius: 10px;
}

.frame-slot img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transform: scale(1);
    transform-origin: center center;
    /* Hide broken image icon and alt text */
    font-size: 0;
    color: transparent;
    text-indent: -9999px;
    background-color: transparent;
    /* Hide default browser placeholder */
    display: block;
}

/* Hide broken images completely */
.frame-slot img[src=""],
.frame-slot img:not([src]) {
    opacity: 0 !important;
    visibility: hidden !important;
    width: 0 !important;
    height: 0 !important;
}

.frame-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    pointer-events: none;
    z-index: 2;
}