.photo-select-wrapper {
    min-height: 100vh;
    width: 100%;
    padding: 40px 20px 120px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 32px;
    background: #f5f5f7;
}

.select-title {
    color: #1d1d1f;
    font-size: 28px;
    font-weight: 600;
    margin: 0;
    letter-spacing: -0.5px;
}

.select-count {
    color: #ff9500;
    font-weight: 600;
}

.slick-slide>div {
    display: flex;
    justify-content: center;
}

.slick-slider {
    width: 100%;
    max-width: 680px;
}

.photo-wrapper {
    padding: 8px;
}

.photo-inner {
    position: relative;
    display: inline-block;
}

.photo-img {
    display: block;
    width: 140px;
    height: 140px;
    object-fit: cover;
    border-radius: 16px;
    cursor: pointer;
    transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.photo-img:hover {
    transform: scale(1.03);
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.12);
}

.photo-img.selected {
    opacity: 0.4;
}

.select-number {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 48px;
    height: 48px;
    background: #ff9500;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 22px;
    font-weight: 600;
    color: #fff;
}

.next-btn {
    position: fixed;
    top: 20px;
    right: 20px;
    width: 44px;
    height: 44px;
    padding: 0;
    font-size: 20px;
    color: #c7c7cc;
    background: #fff;
    border: none;
    border-radius: 50%;
    cursor: not-allowed;
    transition: all 0.2s ease;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
}

.next-btn.active {
    background: #ff9500;
    color: #fff;
    cursor: pointer;
    box-shadow: 0 4px 12px rgba(255, 149, 0, 0.3);
}

.next-btn.active:hover {
    transform: scale(1.05);
    box-shadow: 0 6px 16px rgba(255, 149, 0, 0.4);
}